.custom-select {
  width: 100%;
  color: #333;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-bottom: 10px;
  padding: 10px;
  font-size: 16px;
  transition: border-color .3s;
  display: block;
}

.custom-select:hover, .custom-select:focus {
  border-color: #666;
  outline: none;
}

.custom-loader, .custom-error {
  text-align: center;
  color: #333;
  background-color: #e6dac4;
  border-radius: 4px;
  margin-bottom: 10px;
  padding: 10px;
  font-weight: bold;
  display: none;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .1);
}

.custom-cat-info {
  text-align: center;
  background-color: #fff;
  border-radius: 4px;
  margin-top: 20px;
  padding: 20px;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .1);
}

.custom-cat-info img {
  max-width: 100%;
  height: auto;
  border-radius: 4px;
  margin: 0 auto 20px;
  display: block;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .2);
}

.custom-cat-info .info {
  margin-top: 10px;
}

.custom-cat-info h1.name {
  color: #333;
  margin-bottom: 10px;
  font-size: 28px;
}

.custom-cat-info p {
  color: #555;
  margin-bottom: 10px;
  font-size: 18px;
}

.custom-cat-info p.temperament {
  color: #777;
  font-weight: bold;
}

body {
  background-color: #f6f2e6;
  font-family: Arial, sans-serif;
}

.container {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
}

.title {
  text-align: center;
  color: #333;
  margin-bottom: 20px;
  font-size: 36px;
}

.subtitle {
  text-align: center;
  color: #666;
  margin-bottom: 20px;
  font-size: 24px;
}

